: inline-block;\n        } @else {\n          0% {\n            transform: translateY(5px);\n          }\n          25% {\n            transform: translateY(10px);\n          }\n          50% {\n            transform: translateY(0);\n          }\n          75% {\n            transform: translateY(-10px);\n          }\n          100% {\n            transform: translateY(-5px);\n          }\n        }\n      }\n    }\n  }\n}\n\n.work_list {\n  :global {\n    li {\n      border-left: 5px solid var(--color-default-gray);\n      padding-left: 15px;\n    }\n    a:hover {\n      .tipso {\n        display: block;\n        @include animation-effect() using ($name) {\n          @if ($name) {\n            animation: $name 0.5s ease-in;\n          } @else {\n            from {\n              transform: translateY(-15px);\n            }\n            to {\n              transform: translateY(0);\n            }\n          }\n        }\n      }\n    }\n    .tipso {\n      display: none;\n      top: -55px;\n      left: 0;\n      right: 0;\n      width: 90px;\n      height: 42px;\n      margin: auto;\n      line-height: 42px;\n      border-radius: 6px;\n      > div::after {\n        position: absolute;\n        right: 0;\n        left: 0;\n        margin: auto;\n        bottom: -16px;\n        width: 0;\n        content: '';\n        display: inline-block;\n        border: {\n          style: solid;\n          width: 8px;\n          color: var(--color-primary) transparent transparent transparent;\n        }\n      }\n    }\n  }\n}\n@media (min-width: 960px) {\n  .work_list {\n    :global {\n      li {\n        @include clearfix();\n        border-left: none;\n        padding-left: 0;\n        > div {\n          width: 50%;\n        }\n        &:nth-child(even) {\n          > div {\n            float: left;\n            border-right: 5px solid var(--color-default-gray);\n          }\n          .logo {\n            margin-right: -80px;\n            float: right;\n          }\n         